<template>
    <div>
        <h2 class="reviews__title">Відгуки</h2>
        <table class="table__reviews">
            <thead class="thead__reviews">
                <tr>
                    <th>Ім'я</th>
                    <th>Рейтинг</th>
                    <th>Коментар</th>
                </tr>
            </thead>
            <tbody class="tbody__reviews">
                <tr v-for="(review, index) in reviews" :key="index" >
                    <td>{{review.Name}}</td>
                    <td>{{review.Rating}}</td>
                    <td>{{review.Comment}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>


<script>
    import Papa from 'papaparse';

    export default {
        name: "ReviewsComponent",
        data() {
            return ({
                reviews: [
                    // {Name: "Artem1", Rating: 5, Comment: "Text", },
                    // {Name: "Artem2", Rating: 4, Comment: "Text", },
                    // {Name: "Artem3", Rating: 5, Comment: "Text", },
                    // {Name: "Artem4", Rating: 4, Comment: "Text", }
                ]
            })
        },
        created() {
            const csvContent = `
            Name,Rating,Comment
Іван,5,Дякую за швидку доставку
Ольга,5,Все супер! Буду замовляти ще
Андрій,5,Обслуговування, як завжди - на висоті
Марина,4,Все чудово! Покупкою задоволена, але коробка була прим'ята.
Сергій,5,Рекомендую цей чудовий магазин усім!
Данило,5,Дуже демократичні ціни. Я завжди купую лише тут.
Матвій,5,Дякую за подарунок до покупки - завжди радуєте приємними сюрпризами.
Анна,5,Дякую за чудові знижки
Раймонд,4,Як завжди все кльово, але малий термін гарантії для годинника, хотілося б довше.
            `;

            Papa.parse(csvContent.trim(), {
                header: true,
                complete: (result) => {
                    // console.log("[INFO] - result.data", result.data)
                    this.reviews = result.data
                } 
            })
        }
    }
</script>


<style scoped>
    .reviews__title {
        text-align: center;
    }
    .table__reviews {
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    th,td {
        background-color: #dfdfdf;
    }
    tbody > tr > td:last-child {
        text-align: left;
    }

</style>